"use client";

import { motion } from "framer-motion";
import { Laptop, ShoppingBag, Share2, Video, CheckCircle2 } from "lucide-react";
import Link from "next/link";

const mainServices = [
    {
        slug: "website-development",
        title: "Website Development",
        description: "Senior-level engineering using Next.js, React, and high-performance backend systems. We build sites that are fast, secure, and infinitely scalable.",
        icon: Laptop,
        features: ["Next.js & React Mastery", "Performance Optimization", "SEO-First Architecture", "Secure Backend Integration"],
    },
    {
        slug: "ecommerce-development",
        title: "Ecommerce Development",
        description: "Custom storefronts and Shopify/Headless commerce solutions designed to convert visitors into loyal customers at scale.",
        icon: ShoppingBag,
        features: ["Custom Flow Design", "Secure Checkout Systems", "Inventory Management", "Marketing Automation"],
    },
    {
        slug: "social-media-marketing",
        title: "Social Media Marketing",
        description: "Data-driven marketing strategies that amplify your brand reach and engage your target audience across all platforms.",
        icon: Share2,
        features: ["Targeted Campaigns", "Engagement Analytics", "Content Strategy", "Community Management"],
    },
    {
        slug: "video-editing",
        title: "Video Editing",
        description: "Cinematic post-production that transforms raw footage into compelling stories that resonate with your audience.",
        icon: Video,
        features: ["Cinematic Storytelling", "Motion Graphics", "Color Grading", "Sound Design"],
    },
];

export default function ServicesPage() {
    return (
        <div className="pt-32 pb-20">
            <section className="container-custom mb-20">
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.8 }}
                    className="text-center max-w-4xl mx-auto"
                >
                    <span className="text-indigo-600 font-bold uppercase tracking-widest text-xs mb-6 block">Our Expertise</span>
                    <h1 className="text-5xl md:text-8xl font-black tracking-tighter leading-[0.9] mb-10">
                        Digital Excellence <br />
                        <span className="text-indigo-600">Perfectly</span> Delivered.
                    </h1>
                    <p className="text-xl text-black/60 font-medium">
                        We provide high-impact digital services designed for brands that demand perfection.
                    </p>
                </motion.div>
            </section>

            <section className="container-custom">
                <div className="grid grid-cols-1 gap-12">
                    {mainServices.map((service, index) => (
                        <motion.div
                            key={service.slug}
                            initial={{ opacity: 0, y: 30 }}
                            whileInView={{ opacity: 1, y: 0 }}
                            viewport={{ once: true }}
                            transition={{ delay: index * 0.1 }}
                            className="group bg-white border border-gray-100 rounded-[3rem] p-8 md:p-16 hover:border-indigo-600/20 hover:shadow-2xl hover:shadow-indigo-500/5 transition-all duration-500 flex flex-col lg:flex-row gap-16 items-center"
                        >
                            <div className="lg:w-1/2">
                                <div className="w-20 h-20 bg-indigo-50 rounded-2xl flex items-center justify-center text-indigo-600 mb-8 group-hover:scale-110 transition-transform duration-500">
                                    <service.icon size={36} />
                                </div>
                                <h2 className="text-3xl md:text-5xl font-black tracking-tighter mb-6 uppercase">{service.title}</h2>
                                <p className="text-black/60 text-lg font-medium leading-relaxed mb-8">
                                    {service.description}
                                </p>
                                <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 mb-10">
                                    {service.features.map((feature) => (
                                        <div key={feature} className="flex items-center gap-2">
                                            <CheckCircle2 size={16} className="text-indigo-600" />
                                            <span className="text-sm font-bold text-black/70 uppercase tracking-tight">{feature}</span>
                                        </div>
                                    ))}
                                </div>
                                <Link
                                    href={`/services/${service.slug}`}
                                    className="inline-flex bg-black text-white px-8 py-4 rounded-full font-bold uppercase tracking-widest hover:bg-indigo-600 transition-colors"
                                >
                                    Explore Service
                                </Link>
                            </div>
                            <div className="lg:w-1/2 relative">
                                <div className="aspect-video bg-gray-100 rounded-[2rem] overflow-hidden relative">
                                    <div className="absolute inset-0 bg-gradient-to-br from-indigo-600/5 to-transparent z-10" />
                                    <div className="absolute inset-0 flex items-center justify-center text-8xl font-black text-black/5 select-none tracking-tighter uppercase whitespace-nowrap opacity-50">
                                        {service.title.split(' ')[0]}
                                    </div>
                                </div>
                            </div>
                        </motion.div>
                    ))}
                </div>
            </section>
        </div>
    );
}
